//}
export const restaurarCliente = ( state ) => {
    state.cliente = {}
}

export const setLoading = (state, val) => {

    state.isLoading = val   
}


export const setEdit = (state, val) => {
    if(state.edit){
        state.edit = false
        state.cliente = {}
    }else{
        state.edit = true
        state.cliente = val
    }
}

export const setEditPanel = (state, val) => {
    if(state.edit){
        state.edit = false
    }else{
        state.edit = true
        state.cliente = val
    }
}

export const closeEdit = (state) =>{
    state.cliente = {}
    state.edit = false
}

export const closeEditPanel = (state) =>{
    state.edit = false
}

export const setClientes = ( state, val ) => {
    state.clientes = val
}

export const setPanel = ( state, val ) => {
    state.panel = val
}

export const setCliente = ( state, val ) => {
    state.cliente = val
}

export const updateCliente = ( state, cliente ) => {
    
    const id = state.clientes.map( cli => cli._id).indexOf(cliente._id)
    state.clientes[id] = cliente
}

export const deleteCliente = ( state, _id ) => {
    
    const id = state.clientes.map( cli => cli._id).indexOf(_id)
    state.clientes.splice(id,1)
}

export const addCliente = ( state, cliente ) => {
    state.clientes = [cliente, ...state.clientes]
}



