export const setIsLoading = ( state, val ) => {
    state.isLoading = val
}

export const setFacturas = ( state, facturas ) => {

    state.facturas = facturas
}

export const setSuma = ( state, suma ) => {

    state.suma = suma
}

export const setFactura = (state, factura) =>{
    state.factura = factura
    state.editFactura = true
}

export const addFactura = (state, factura) => {
    state.facturas = [factura, ...state.facturas]
}


export const updateFactura = ( state, factura ) => {
    
    const id = state.facturas.map( fac => fac._id).indexOf(factura._id)
    state.facturas[id] = factura
}

export const deleteFactura = ( state, _id ) => {
    
    const id = state.facturas.map( fac => fac._id).indexOf(_id)
    state.facturas.splice(id,1)
}

export const salirEdit = (state) => {
    state.editFactura = false
    state.factura = {}
}


export const setIdNumero = (state, numero) => {
    state.idNumero = numero

}
