export const getIngresos = async ({commit}) => {
    
    try {
        commit('setLoading', true)
        
        const respuesta = await fetchConToken('numero/ingresos')
        const body = await respuesta.json();
        
        if(body.ok){
    
            commit('setIngresos', body.ingresos)
            commit('setLoading', false)
        }

    } catch (error) {
        console.log(error)
        commit('setLoading', false)
        
    }
   
}