import { fetchConToken } from "@/help/fetch";
import Swal from "sweetalert2";

export const getFacturas = async ({commit},id) => {

    try {
        commit('setIsLoading', true)
        const respuesta = await fetchConToken(`factura/numero/${id}`)
        const body = await respuesta.json();

        if(body.ok){
            commit('setFacturas', body.facturas)
            commit('setIsLoading', false)
                           
        }

    } catch (error) {
        console.log(error)
        
    }
}

export const addFactura = async ({commit}, factura) => {
    try {
        const respuesta = await fetchConToken('factura/new', factura , 'POST')
        const body = await respuesta.json();
   
        if(body.ok){
            commit('addFactura', body.factura)
            const Toast = Swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
                }
              })

              
              
              Toast.fire({
                icon: 'success',
                title: 'Registro Exitoso'
              })                     
      
        }else{

            Swal.fire({
            title: 'Error!',
            text: `${body.msg}`,
            icon: 'error',
            confirmButtonText: 'Cool'
            })    
        }
    } catch (error) {
        console.log(error)
    }
}


export const updateFactura = async ({commit}, factura) => {
    try {
        
        const respuesta = await fetchConToken(`factura/${factura._id}`,factura, 'PUT')
        const body = await respuesta.json();
        
        
        if(body.ok){
         commit('updateFactura', factura)  
         commit('salirEdit')
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Ha sido actualizado',
            showConfirmButton: false,
            timer: 1000
          })
        }
  
         
      } catch (error) {
  
        Swal.fire({
                  title: 'Error!',
                  text: `${error}`,
                  icon: 'error',
                  confirmButtonText: 'Cool'
                })
      }
}


export const deleteFactura = async ({commit}, factura) => {

    const swalWithBootstrapButtons = Swal.mixin({
        customClass: {
          confirmButton: 'btn btn-success',
          cancelButton: 'btn btn-danger'
        },
        buttonsStyling: false
      })
      
      swalWithBootstrapButtons.fire({
        title: '¿ Estas seguro?',
        text: "Si eliminas no podras recuperar la informacion!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, Eliminar!',
        cancelButtonText: 'No, cancelar!',
        reverseButtons: true
      }).then(async (result) => {
        if (result.isConfirmed) {

            const respuesta = await fetchConToken( `factura/${factura._id}`,factura,'DELETE');
            const body = await respuesta.json()
            if(body.ok){
                
                swalWithBootstrapButtons.fire(
                    'Eliminado!',
                    'Su registro ha sido eliminado',
                    'success'
                )
                commit('deleteFactura', factura._id)
                //await getSocios();
            }

        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
            swalWithBootstrapButtons.fire(
                'Cancelado',
                'Sus registros siguen intactos :)',
            'error'
          )
        }
    })
}

