    component: () => import(/* webpackChunkName: 'auth' */ '@/modules/auth/layouts/AuthLayout'),
    children: [     
        {
            path:'',
            name:'auth-home',
            component: () => import(/* webpackChunkName: 'Home' */ '@/views/HomeView.vue'),
        },
        {
            path:'/login',
            name:'auth-login',
            component: () => import(/* webpackChunkName: 'login' */ '@/modules/auth/views/Login'),
        },
        {
            path:'/numeros',
            name:'auth-numeros',
            component: () => import(/* webpackChunkName: 'Numeros' */ '@/modules/rifa/views/Numeros.vue'),
        },
    ]
