export const setLoading = (state, val) => {
    state.isLoading = val   
}

export const setNumeros = ( state, numeros ) => {
    state.numeros = numeros
}




export const setNumero = ( state, numero ) => {
    state.numero = numero
}

export const setNumerosCliente = ( state, numeros ) => {
    state.numerosCliente = numeros
}

export const addNumero = ( state, numero ) => {
    state.numerosCliente = [numero, ...state.numerosCliente]
}

export const deseleccionarNumero = ( state, _id ) => {
    
    const id = state.numerosCliente.map( num => num._id).indexOf(_id)
    state.numerosCliente.splice(id,1)
}

