import { createStore } from "vuex";
import authModule from "@/modules/auth/store/auth";
import clienteModule from "@/modules/rifa/store/cliente"
import numerosModule from "@/modules/rifa/store/numeros";
import facturarModule from "@/modules/rifa/store/facturar";
import cajaModule from "@/modules/rifa/store/caja";

const store = createStore ({
    modules:{
        auth: authModule,
        cliente: clienteModule,
        numeros: numerosModule,
        facturar : facturarModule,
        caja: cajaModule
    }         
})

export default store