import Swal from 'sweetalert2'
    
import { fetchConToken } from "@/help/fetch";


export const getNumeros = async ({commit}) => {
    
    try {
        commit('setLoading', true)
        
        const respuesta = await fetchConToken('numero')
        const body = await respuesta.json();
        
        if(body.ok){
    
            commit('setNumeros', body.numeros)
            commit('setLoading', false)
        }

    } catch (error) {
        console.log(error)
        commit('setLoading', false)
        
    }
   
}

export const getNumero = async ({commit}, id) => {
    
    try {
        commit('setLoading', true)
        
        const respuesta = await fetchConToken( `numero/${id}`);
        const body = await respuesta.json();
        
        if(body.ok){
      
            commit('setNumero', body.numero)
            commit('setLoading', false)
        }else{
            Swal.fire({
                position: 'center',
                icon: 'error',
                title: 'Numero no existe',
                showConfirmButton: false,
                timer: 1000
              })
        }

    } catch (error) {
        console.log(error)
        commit('setLoading', false)
        
    }
   
}


export const updateNumero = async ({commit}, numero) => {
    try {
        
        const respuesta = await fetchConToken(`numero/${numero.id}`,numero, 'PUT')
        const body = await respuesta.json();
        
        
        if(body.ok){
         commit('addNumero', body.numero)  
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Ha sido actualizado',
            showConfirmButton: false,
            timer: 1000
          })
        }
  
         
      } catch (error) {
  
        Swal.fire({
                  title: 'Error!',
                  text: `${error}`,
                  icon: 'error',
                  confirmButtonText: 'Cool'
                })
      }
}

