export const getClientes = async ({commit}) => {
    
    try {
        commit('setLoading', true)
        
        const respuesta = await fetchConToken('cliente')
        const body = await respuesta.json();
        
        if(body.ok){
            commit('setClientes', body.clientes)
            commit('setLoading', false)
        }

    } catch (error) {
        console.log(error)
        commit('setLoading', false)
        
    }
   
}

export const getCliente = async ({commit}, id) => {
    
  try {
      commit('setLoading', true)
      
      const respuesta = await fetchConToken(`cliente/${id}`)
      const body = await respuesta.json();
      
      if(body.ok){
          commit('setCliente', body.cliente)
          commit('setLoading', false)
      }

  } catch (error) {
      console.log(error)
      commit('setLoading', false)
      
  }
 
}

export const addCliente = async ({commit}, cliente) =>  {
    try {

        const respuesta = await fetchConToken('cliente/new', cliente , 'POST')
        const body = await respuesta.json();
 
        if(body.ok){
            cliente._id = body.cliente._id
            const Toast = Swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener('mouseenter', Swal.stopTimer)
                  toast.addEventListener('mouseleave', Swal.resumeTimer)
                }
              })  
              
              Toast.fire({
                icon: 'success',
                title: 'Registro Exitoso'
              })
            
              commit('addCliente', cliente)

        }else{

            Swal.fire({
            title: 'Error!',
            text: `${body.msg}`,
            icon: 'error',
            confirmButtonText: 'Cool'
            })    
        }
    } catch (error) {
        console.log(error)
    }
}

export const updateCliente = async ({commit}, cliente) => {
    try {

        const respuesta = await fetchConToken(`cliente/${cliente._id}`,cliente, 'PUT')
        const body = await respuesta.json();
        
        
        if(body.ok){
         commit('updateCliente', cliente)  
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Ha sido actualizado',
            showConfirmButton: false,
            timer: 1000
          })
        }
  
         
      } catch (error) {
  
        Swal.fire({
                  title: 'Error!',
                  text: `${error}`,
                  icon: 'error',
                  confirmButtonText: 'Cool'
                })
      }
}

export const deleteCliente = async ({commit}, id) => {
    
    const swalWithBootstrapButtons = Swal.mixin({
        customClass: {
          confirmButton: 'btn btn-success',
          cancelButton: 'btn btn-danger'
        },
        buttonsStyling: false
      })
      
      swalWithBootstrapButtons.fire({
        title: '¿ Estas seguro?',
        text: "Si eliminas no podras recuperar la informacion!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, Eliminar!',
        cancelButtonText: 'No, cancelar!',
        reverseButtons: true
      }).then(async (result) => {
        if (result.isConfirmed) {

            
            const respuesta = await fetchConToken( `cliente/${id}`,{},'DELETE');
            const body = await respuesta.json()
            if(body.ok){
                
                swalWithBootstrapButtons.fire(
                    'Eliminado!',
                    'Su registro ha sido eliminado',
                    'success'
                )
                commit('deleteCliente', id)
                //await getSocios();
            }

            
                
                

        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
            swalWithBootstrapButtons.fire(
                'Cancelado',
                'Sus registros siguen intactos :)',
            'error'
          )
        }
    })
    
}

