export default {

    name: 'rifa',
    component: () => import(/* webpackChunkName: 'auth' */ '@/modules/rifa/layouts/RifaLayout.vue'),
    children: [ 
        {
            path:'',
            name:'rifa-cliente',
            component: () => import(/* webpackChunkName: 'rifa-cliente' */ '@/modules/rifa/views/Cliente.vue'),
        },   
        {
            path:'/home',
            name:'rifa-home',
            component: () => import(/* webpackChunkName: 'rifa-home' */ '@/views/HomeView.vue'),
        }, 
        
        {
            path:'/numerosprivado',
            name:'rifa-numerosprivado',
            component: () => import(/* webpackChunkName: 'rifa-numerosPrivado' */ '@/modules/rifa/views/NumerosPrivado.vue'),
        },
        {
            path:'cliente/:id',
            name:'rifa-panel',
            component: () => import(/* webpackChunkName: 'rifa-panel' */ '@/modules/rifa/views/PanelCliente.vue'),
            props:(route)=>{
                var {id} = route.params
                return {id}
            }
        },
        {
            path:'facturar/:id',
            name:'rifa-facturar',
            component: () => import(/* webpackChunkName: 'rifa-facturar' */ '@/modules/rifa/views/FacturarNumero.vue'),
            props:(route)=>{
                const {id} = route.params
                return{id}
            }
        },
        {
            path:'/caja',
            name:'rifa-caja',
            component: () => import(/* webpackChunkName: 'rifa-caja' */ '@/modules/rifa/views/Caja.vue'),
        },
    ]
}