import { fetchSinToken } from "@/help/fetch";
import Swal from "sweetalert2";

export const startLogin = async ({commit}, user) => {
        const {usuario, password} = user

    try {
        const respuesta = await fetchSinToken('usuario/login', { usuario, password }, 'POST');
        const body = await respuesta.json();

        
        if(body.ok){
            localStorage.setItem('token', body.token);
            localStorage.setItem('token-init-date', new Date().getTime());
            commit('setLogin',{uid: body.uid, perfil: body.perfil})
        }else {
            Swal.fire('Error', body.msg, 'error')
        }
        
    } catch (error) {
        console.log(error)
    }
}

